import React from 'react';

import Physics1 from '../../assets/img/physics1.jpg';
import Physics2 from '../../assets/img/physics2.jpg';
import Physics3 from '../../assets/img/physics3.jpg';
import Physics4 from '../../assets/img/physics4.jpg';
import Physics5 from '../../assets/img/physics5.jpg';

import { Link } from 'react-router-dom';
import Footer from '../../components/Footer';

const SciencePhysics = () => {
	return (
		<div>
			<div className='science-background science-background--physics'>
				<h2>FIZYKA</h2>
				<Link to='/nauki-scisle-po-godzinach'>NAUKI CISE PO GODZINACH</Link>
			</div>
			<div className='science-content'>
				<h3>
					Fizyka dla uczniw i uczennic
					<br /> ze szk podstawowych
				</h3>
				<div className='science-physics section-1'>
					<div className='section'>
						<p className='science-text'>
							Tutaj fizyk zaczniemy od podstaw i to podstaw matematycznych.
							<br />
							<br />
							<bold className='science-color-physics'>
								Naszym celem jest ukierunkowaniu uczniw na zainteresowanie i
								zrozumienie zagadnie technicznych w przyszoci,
							</bold>
							<br />
							<br />
							wic na pocztku bdziemy przeksztaca wzory, dodawa wektory
							oraz zajmowa si t czci matematyki, ktra bdzie nam
							niezbdna na fizyce. Z czasem pojawi si nawet funkcje
							trygonometryczne.
							<br />
							<br />
							Rwnolegle obok matematyki, bdzie raczkowao zrozumienie
							zagadnie z fizyki. Pocztkowo bdzie si to opierao gwnie na
							opowiadaniu bajek, ale wraz ze wzrostem umiejtnoci
							matematycznych coraz odwaniej bdziemy zagbiali si we wzory,
							precyzyjne rysunki i obliczenia, czyli to jak fizyka powinna
							wyglda w szkole podstawowej.
						</p>
					</div>
					<div className='section section-img'>
						<img src={Physics1} alt='Zajcia z fizyki.' />
					</div>
					<div className='section'>
						<p className='science-text'>
							Naturalnym zwieczeniem przygody z fizyk na tym etapie bdzie
							przygotowanie uczniw do udziau w kuratoryjnym konkursie z
							fizyki, ktry jest wielk motywacj do wytonej pracy, a w
							przypadku sukcesu
							<br />
							<br />
							<bold className='science-color-physics'>
								otwiera przed uczniami drog do dowolnej szkoy ponadpodstawowej
								w Polsce.
							</bold>
						</p>
					</div>
					<div className='section section-img'>
						<img src={Physics2} alt='Zajcia z fizyki.' />
					</div>
					<div className='section'>
						<ul>
							Dodatkowo oprcz fizyki odbywaj si:
							<br />
							<br />
							<li>
								<bold>zajcia z matematycznych podstaw fizyki</bold> (dwa razy w
								miesicu)
							</li>
							<li>
								<bold>
									zajcia oraz wykady z pracownikiem naukowym Uniwersytetu
									Morskiego w Gdyni
								</bold>{' '}
								(dwa razy w roku szkolnym)
							</li>
							<li>
								<bold>a take wyjazd na Uniwersytet Morski w Gdyni </bold>(jeden
								w trakcie roku szkolnego)
							</li>
						</ul>
					</div>
					<div className='section'>
						<p className='science-text'>
							Zajcia skierowane s do modych ludzi{' '}
							<bold>ze szk podstawowych.</bold>
							<br />
							<br />
							Zapisy przyjmujemy cay rok szkolny.
							<br />
							<br />
							Pierwsze grupy startuj w <bold>padzierniku 2022 r.</bold>
							<br />
							<br />
							Zajcia odbywaj si:
						</p>
						<div className='science-classes'>
							<div>w maych grupch</div>
							<div>raz w tygodniu</div>
							<div>trwaj 90 minut</div>
						</div>
					</div>
				</div>
			</div>
			<div className='science-separator physics-separator'></div>
			<div className='science-content'>
				<h3>
					Fizyka dla uczniw i uczennic
					<br />
					ze szk ponadpodstawowych
				</h3>
				<div className='science-physics section-2'>
					<div className='section'>
						<p className='science-text'>
							W zdecydowanej wikszoci szk rednich fizyka realizowana jest
							tylko na poziomie podstawowym. Rodzi to problem przy rekrutacji na
							studia techniczne, gdzie coraz czciej wymagana jest matura z
							fizyki, a ta przeprowadzana jest tylko na poziomie rozszerzonym.
						</p>
						<ul>
							Zajcia z fizyki na tym etapie prowadzi bdziemy kompleksowo:
							<li>przechodzc przez kolejne dziay fizyki,</li>
							<li>rozkadajc zagadnienia na podstawowe skadniki,</li>
							<li>
								wprowadzajc na bieco odpowiedni aparat matematyczny, ktry
								pozwoli na zrozumienie zarwno na poziomie intuicyjnym jak i
								matematycznym fizyki.
							</li>
						</ul>
						<p className='science-text'>
							<bold className='science-color-physics'>
								W trakcie kursu przygotujemy do zdawania matury rozszerzonej z
								fizyki oraz pokaemy, e rekrutacja na kierunki techniczne na
								studia nie jest taka trudna.
							</bold>
						</p>
					</div>
					<div className='section section-img'>
						<img src={Physics3} alt='Zajcia z fizyki.' />
					</div>
					<div className='section'>
						<ul>
							Dodatkowo oprcz fizyki odbywaj si:
							<br />
							<br />
							<li>
								<bold>zajcia z matematycznych podstaw fizyki</bold> (dwa razy w
								miesicu)
							</li>
							<li>
								<bold>
									zajcia oraz wykady z pracownikiem naukowym Uniwersytetu
									Morskiego w Gdyni
								</bold>
								(dwa razy w roku szkolnym)
							</li>
							<li>
								<bold>a take wyjazd na Uniwersytet Morski w Gdyni</bold> (jeden
								w trakcie roku szkolnego)
							</li>
						</ul>
					</div>
					<div className='section'>
						<p className='science-text'>
							<br />
							Zajcia skierowane s do modych ludzi{' '}
							<bold>ze szk ponadpodstawowych.</bold>
							<br />
							<br />
							Zapisy przyjmujemy cay rok szkolny.
							<br />
							<br /> Pierwsze grupy startuj w <bold>padzierniku 2022 r.</bold>
							<br />
							<br />
							Zajcia odbywaj si:
						</p>
						<div className='science-classes'>
							<div>w maych grupch</div>
							<div>raz w tygodniu</div>
							<div>trwaj 90 minut</div>
						</div>
					</div>
				</div>
			</div>
			<div className='science-separator physics-separator'></div>
			<div className='science-content'>
				<h3>Fizyka dla maturzystw i maturzystek</h3>
				<div className='science-physics section-3'>
					<div className='section'>
						<div className='science-text'>
							<bold className='science-color-physics'>
								Wybierasz si na studia techniczne i wanie si zorientowae,
								e musisz zdawa matur z fizyki?
							</bold>
							<br />
							<br />
							Miae w szkole tylko fizyk na poziomie podstawowym? A moe
							chcesz usystematyzowa swoj wiedz i jeszcze raz po kolei
							przerobi ca fizyk w szkole przed matur? Te zajcia s dla
							Ciebie! <br />
							<br />
							Na tych zajciach przygotujemy Ci do matury rozszerzonej z
							fizyki. Przerobimy kady dzia po kolei zwracajc szczeglna uwag
							na elementy wyjtkowo wane z punktu widzenia matury. Pomoemy Ci
							zrozumie poszczeglne zalenoci oraz nauczymy Ci pracowa na
							maturze z tablicami wzorw, ktre s tam dostpne.
							<br />
							<br />
							<bold className='science-color-physics'>
								To bdzie intensywny czas, ktry pozwoli Ci nadrobi zalegoci
								z poprzednich lat i pozwoli ze spokojn gow podej do matury.
							</bold>
						</div>
					</div>
					<div className='section section-img'>
						<img src={Physics4} alt='Zajcia z fizyki.' />
					</div>
					<div className='section'>
						<p className='science-text'>
							Zajcia skierowane s do{' '}
							<bold>
								maturzystw chccych zdawa fizyk na poziomie rozszerzonym.
							</bold>
							<br />
							<br />
							Zapisy przyjmujemy cay rok szkolny.
							<br />
							<br /> Pierwsze grupy startuj w<bold>padzierniku 2022 r.</bold>
							<br />
							<br />
							Zajcia odbywaj si:
						</p>
						<div className='science-classes'>
							<div>w maych grupch</div>
							<div>raz w tygodniu</div>
							<div>trwaj 120 minut</div>
						</div>
					</div>
				</div>
			</div>
			<div className='science-separator physics-separator'></div>
			<div className='science-content'>
				<h3>
					Fizyka dla studentw i studentek pierwszych lat na kierunkach
					technicznych
				</h3>
				<div className='science-physics section-4'>
					<div className='section'>
						<p className='science-text'>
							<bold className='science-color-physics'>
								Jeste na pierwszym roku studiw technicznych?
							</bold>
							<br />
							<br />
							Fizyka jest zupenie inna, ni do tej pory? Nie wiesz co si
							dzieje na tablicy i dlaczego cay zapis matematyczny jest zupenie
							inny? Co to s te pochodne i caki? Pomoemy Ci. <br />
							<br />
							<bold className='science-color-physics'>
								Pokaemy, e fizyka, ktra tutaj si pojawia to jest ta sama
								fizyka, ktr znae w szkole redniej,
							</bold>
							<br />
							<br />
							tylko zapis matematyczny si zmieni. Wytumaczymy Ci jak
							wykorzystywa pochodne i caki w zapisie fizycznym i pokaemy, e
							to tak naprawd jest to bardzo wygodne narzdzie w twojej
							przyszej pracy jako inyniera.
						</p>
					</div>
					<div className='section section-img'>
						<img src={Physics5} alt='Zajcia z fizyki.' />
					</div>
					<div className='section'>
						<p className='science-text'>
							Zajcia skierowane s do{' '}
							<bold>studentw pierwszych lat na kierunkach technicznych.</bold>
							<br />
							<br />
							Zapisy przyjmujemy cay rok szkolny.
							<br />
							<br />
							Pierwsze grupy startuj w<bold>padzierniku 2022 r.</bold>
							<br />
							<br />
							Zajcia odbywaj si:
						</p>
						<div className='science-classes'>
							<div>w maych grupch</div>
							<div>raz w tygodniu</div>
							<div>trwaj 120 minut</div>
						</div>
					</div>
				</div>
			</div>
			<div className='science-separator physics-separator'></div>
			<Footer />
		</div>
	);
};

export default SciencePhysics;
