import React from 'react';

const OfferTables = () => {
  return (
    <>
      <div className="offer__section2">
        <div className="offer__term">
          <table>
            <tbody>
              <tr>
                <th className="term__header">Termin</th>
                <th className="term__header">Cena</th>
              </tr>
              <tr>
                <td className="term__row">10 - 17.07.2022r.</td>
                <td className="term__row">1890,00 z</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div className="offer__section3">
        <div className="offer__addtional">
          <table>
            <tbody>
              <tr>
                <th colSpan="2" className="term__header ">
                  Dodatkowe informacje
                </th>
              </tr>
              <tr>
                <td className="term__row">liczba noclegw</td>
                <td className="term__row">7</td>
              </tr>
              <tr>
                <td className="term__row">transport</td>
                <td className="term__row">
                  We wasnym zakresie (istnieje moliwo zorganizowania
                  transportu za dodatkow opat. Cena uzaleniona jest od
                  miejsca zamieszkania uczestnika)
                </td>
              </tr>
              <tr>
                <td className="term__row">atrakcje dodatkowe w cenie obozu</td>
                <td className="term__row">
                  Spyw kajakowy, plaa w bezporednim ssiedztwie hotelu,
                  boisko do siatkwki plaowej
                </td>
              </tr>
              <tr>
                <td className="term__row">zakwaterowanie</td>
                <td className="term__row">
                  Pokoje dwu, trzy i cztero-osobowe w budynku hotelowym
                </td>
              </tr>
              <tr>
                <td className="term__row">posiki</td>
                <td className="term__row">niadanie, obiad, kolacja</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
};

export default OfferTables;
