import React from 'react';

import { Swiper, SwiperSlide } from 'swiper/react/swiper-react'; // problem with route - added  '/swiper-react' at the end
import SwiperCore, { Navigation, Pagination } from 'swiper';

import PascoLogo from '../assets/img/pascoLogo.png';
import PascoSlider1 from '../assets/img/pascoSlider1.jpg';
import PascoSlider2 from '../assets/img/pascoSlider2.jpg';
import PascoSlider3 from '../assets/img/pascoSlider3.jpg';
import Triangle from '../assets/img/triangle.svg';

SwiperCore.use([Navigation, Pagination]);

const PascoContent = () => {
  return (
    <>
      <div className="about about-pasco">
        <h2 className="about--header header">O WSPPRACY</h2>
        <p className="about--text ">
          Osada Wiedzy w 2021r. nawizaa wspprac z firm PASCO z Kalifornii
          w USA, ktra od wielu lat specjalizuje si w rozwoju technologii
          sucej nauczaniu przedmiotw przyrodniczych przez wykonywanie
          dowiadcze i jest w tej dziedzinie wiatowym liderem. PASCO to firma,
          ktra od 1964 roku zajmuje si wycznie projektowaniem i produkcj
          urzdze przeznaczonych specjalnie do zastosowa edukacyjnych.
          <br />
          <br />
          Nasza wsppraca zostaa zainicjowana wraz z rozpoczciem unikalnego
          projektu Fizyka dla nieletnich (www.underage.pro), ktry uzyska
          honorowy patronat Ministra Edukacji i Nauki i jest przeznaczony dla
          nauczycieli fizyki, przedmiotw zawodowych oraz dla uczniw.
        </p>
        <p className="about--text ">
          Mentorzy Osady Wiedzy Ignacy Rejmak oraz Przemysaw Rojewski tworzc
          doskonay, peen humoru i dystansu do siebie duet, prowadz i
          realizuj dowiadczenia wcigajc uczniw w niezwyk przestrze
          fizyki, ktra jest ich pasj i drog zawodow.
          <br />
          <br />
          Partnerem projektu Fizyka dla nieletnich jest Fundacja PGE, ktra od
          lat wspiera wejherowskiego Elektryka, w ktrym pracuj Ignacy i
          Przemysaw. Dla spki to inwestycja w przyszo. W ramach wsppracy
          PGE wyposaya szkoln pracowni w najnowoczeniejszy w Europie sprzt
          edukacyjny firmy PASCO, niezbdny do ksztacenia praktycznego w wielu
          zawodach.
        </p>
        <p className="about--text ">
          PASCO oferuje przede wszystkim cyfrowe pomoce dydaktyczne do
          wykonywania dowiadcze przyrodniczych dla szk podstawowych,
          rednich i dla wyszych uczelni. Niektre urzdzenia ciesz si take
          zainteresowaniem rodzicw, ktrzy pragn inwestowa w rozwj
          kreatywnoci swoich dzieci.
          <br />
          <br />
          Od pewnego czasu ofert PASCO uzupeniaj te urzdzenia do nauki
          kodowania i robotyki. Obsugiwane s przez ten sam program, co
          czujniki do dowiadcze, wic razem tworz jedno, wsplne,
          uzupeniajce si wzajemnie rodowisko edukacyjne.
        </p>
      </div>
      <div className="pasco-tutors">
        <div className="pasco-slider">
          <Swiper
            slidesPerView={1}
            navigation={{
              nextEl: '.nextNav',
              prevEl: '.prevNav',
            }}
            loop={true}
            pagination={{
              el: '.pagination',
              clickable: true,
            }}
            grabCursor
          >
            <div className="swiper-nav">
              <div className="pagination"></div>
            </div>
            <div className="prevNav">
              <img src={Triangle} alt="Strzaki suce do przewijania" />
            </div>

            <div className="nextNav">
              <img src={Triangle} alt="Strzaki suce do przewijania" />
            </div>
            <SwiperSlide>
              <div className="slider-slide">
                <img src={PascoSlider1} alt="Poznaj naszych prowadzcych!" />
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="slider-slide">
                <img src={PascoSlider2} alt="Poznaj naszych prowadzcych!" />
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="slider-slide">
                <img src={PascoSlider3} alt="Poznaj naszych prowadzcych!" />
              </div>
            </SwiperSlide>
          </Swiper>
          <p className="about--text">
            Na stronie{' '}
            <a href="//underage.pro/fizyka/doswiadczenia">
              underage.pro/fizyka/doswiadczenia
            </a>{' '}
            moecie zobaczy w jaki sposb wykorzystujemy urzdzenia m. in.
            PASCO do wykonywania dowiadcze na zajciach.{' '}
          </p>
        </div>
      </div>
      <div className="about about-pasco">
        <h2 className="about--header header">OFERTA DLA RODZICW</h2>
        <p className="about--text ">
          Najwikszym zainteresowaniem rodzicw ciesz si:
          <ul>
            <li>
              PS-3209  Bezprzewodowy czujnik pogody z GPS wraz z PS-3553 
              Akcesoria wiatromierza do czujnika pogody
            </li>
            <li>PS-3201  Bezprzewodowy czujnik temperatury</li>
            <li>PS-3207  Bezprzewodowy czujnik ttna wysikowy</li>
            <li>PS-3228  Bezprzewodowy czujnik wilgotnoci gleby</li>
            <li>PS-3219  Bezprzewodowy czujnik pooenia</li>
            <li>PS-3213  Bezprzewodowy czujnik wiata</li>
            <li>PS-3231  //code.Node</li>
            <li>
              Zestaw do budowy i badania mostw z kilkoma tensometrami (PS-3216
              Bezprzewodowy tensometr i akcelerometr)
            </li>
          </ul>
          a take wszystkie urzdzenia do kodowania i robotyki.
        </p>
        <h2 className="about--header header">OFERTA DLA SZK</h2>
        <p className="about--text ">
          Obecnie, najwikszym zainteresowaniem ciesz si zestawy przygotowane
          w ramach rzdowego dofinansowania Laboratoria Przyszoci. Obecnie
          jest ono dostpne dla publicznych szk podstawowych, a wkrtce bdzie
          te dotyczyo szk niepublicznych i ponadpodstawowych.
          <br />
          <br />
          Szczegy na stronie:{' '}
          <a href="//pasco.com.pl/laboratoria-przyszlosci">
            pasco.com.pl/laboratoria-przyszlosci
          </a>
          .
          <br />
          <br />
          Szkoy zainteresowane zakupem pomocy dydaktycznych PASCO najczciej
          odwiedzaj stron{' '}
          <a href="//pasco.com.pl/co-kupic">pasco.com.pl/co-kupic</a> i
          wybieraj spord gotowych zestaww przygotowanych w oparciu o
          obowizujc podstaw programow dla szk podstawowych i rednich
          oraz do kadego z przedmiotw przyrodniczych. Zestawy mona jednak
          dowodnie modyfikowa lub uoy z dostpnych elementw samodzielnie
          swj wasny.
        </p>
        <div className="about--logo">
          <p className="about--text pasco--text">
            I najwaniejsze  Na haso Osada Wiedzy klienci indywidualni i
            szkoy otrzymaj 10% zniki na zakupy w PASCO.
            <br />
            <br />
            <thin> Zniki nie kumuluj si z innymi.</thin>
          </p>
          <div className="pasco-logo">
            <a href="//pasco.com.pl">
              <img
                src={PascoLogo}
                alt="Logo projektu 'Fizyka dla nieletnich'"
              />
            </a>
          </div>
        </div>
      </div>
    </>
  );
};

export default PascoContent;
