import React from 'react';

const ColonyContent = () => {
  return (
    <div className="about">
      <div className="about-section">
        <h2 className="about--header header">O NAS</h2>
        <p className="about--text">
          Jest rok 2022. Cay wiat zdominowany zosta przez Mocarstwo
          Edukacyjnej Negacji. Cay? Nie. Jedna jedyna osada, prowadzona przez
          nieugitych mentorw wci stawia opr najedcom i walczy dzielnie
          rzucajc magicznym pyem wyobrani i pasji, aby wrzuceni w otcha
          testw, sprawdzianw, ocen i schematw modzi mistrzowie stali si
          sojusznikami umysu. Osada wiedzy przyciga swym blaskiem wszystkich
          zafascynowanych naukami cisymi wojownikw, aby mogli oni zdobywa
          skarby bdce ich orem w drodze do sukcesu. Zalepieni wiar w nauk
          porzucaj letnie, bogie lenistwo i ruszaj na podbj dyscyplin
          matematyki, fizyki i programowania. W atmosferze rodzinnych relacji
          rozpoczynaj kady dzie od wiczenia tyzny fizycznej, aby nastpnie
          poruszy bystry umys do zdobywania wszelkiej mdroci. Wyposaeni w
          pewno siebie i odwag wyruszaj z osady z wiar w sukces,
          przekonani, e wyobrania nie zna granic i zawsze jest waniejsza od
          wiedzy, lecz wiedzc jednoczenie, e wiedza jest moc a moc jest
          wiedz. Osada wiedzy jest ich sojusznikiem a oni s sojusznikami
          zdominowanych przez Mocarstwo dajc im wiato na lepsze czasy.
        </p>
      </div>
      <div className="about-section">
        <div className="about--header header">OBEJRZYJ</div>
        <iframe
          title="movie"
          className="frame frame-colony"
          src="https://www.youtube.com/embed/rYot1-OtgTw"
          frameBorder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
      </div>
    </div>
  );
};

export default ColonyContent;
