import React, { useState, useEffect } from 'react'

import Book from '../assets/img/bookBig.jpg'
import BookSmall from '../assets/img/bookSmall.jpg'

const RecomendationsContent = () => {
    const [windowWidth, setWindowWidth ] = useState(window.innerWidth);
    const imageUrl = windowWidth < 1050 && windowWidth > 600 ? BookSmall : Book
    useEffect(() => {
        window.addEventListener('resize', handleWindowResize);

        return () => {
            window.removeEventListener('resize', handleWindowResize);
        }
    });
    const handleWindowResize = () => {
        setWindowWidth(window.innerWidth);
    };
    return (
        <div className="recomendations">
            <img src={imageUrl} alt='Book' className='recomendations__img'/>
            <h2 className='header recomendations__header'>Rekomendacje</h2>
            <div className="recomendations__container">
                <div className="recomendations__section">
                    <div className="section__text">"W trakcie obozu czuj si rozbawiony i czekam na kolejne atrakcje, natomiast tu po obozie czuj satysfakcj z tego czego si nauczyem i dokonaem w jego trakcie. Wanym elementem jest atmosfera panujc wrd nauczycieli i uczniw, dziki ktrej tak na prawd kady moe znale swoje miejsce i poczu si czci zgranej grupy"</div>
                    <div className='section__author'><br></br>Janek Niedziela</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">W trakcie obozu czuam si szczliwa i zafascynowana rzeczami, ktrych moglimy si nauczy. Panuje tu wspaniaa atmosfera, jest duo ciekawych ludzi, duo wyzwa, s genialne spywy.  Ludzie, ktrzy tworz ten obz sprawiaj, e mona na nim pozna najlepszych przyjaci. Gdybym miaa podsumowa obz napisaabym, e uczenie si moe by zabawne.</div>
                    <div className='section__author'><br></br>Dominika Urbaska</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">W trakcie obozu czsto czuem si zmotywowany, zainspirowany, zaciekawiony. Po obozie byo mi smutno, ale te byem chtny do dziaania, rozwoju. Moim zdaniem filarem tego obozu jest rodzinna atmosfera, obz tworz ludzie. Gdybym mia stwierdzi co jest misj tego obozu, napisabym, e sowa: Dowiedz si, co chcesz robi w yciu i naucz si uczy. W mojej opinii kontakt z kadr obozu sprawia, e tacy modzi ludzie jak ja, zaczynaj pragn zdobywa wiedz poza programem nauczania, poza to, co jest wymagane w szkole i to nie tylko z matematyki i fizyki.</div>
                    <div className='section__author'><br></br>Jakub Klawikowski</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">Jako uczestnik w czasie obozu czuem si szczliwy. Kiedy wracam do domu zawsze jestem zadowolony ze wspaniale spdzonego czasu. Myl, e filarem obozu jest poczenie przystpnie podanej nauki ze wietn atmosfer tworzon przez wspaniaych ludzi i zabaw. Uwaam dzi, e nauka moe by ciekawa i przyjemna.</div>
                    <div className='section__author'><br></br>Maciej Wrblewski</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">Moim zdaniem obz naukowy to miejsce z najlepsz kadr na wiecie. Modzi ludzie przyjedaj tu, bo chc si doskonali. Chc spdza czas z fizyk i dobrze si bawi. Tu kady z nas doskonali si na rnych paszczyznach, naukowej o osobistej. Dzieje si to jednak w poczeniu z zabaw.</div>
                    <div className='section__author'><br></br>Marcin Kosiski</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">W trakcie obozu zawsze byem zmotywowany do zdobywania wiedzy. Kadego dnia byem gotowy na kolejne atrakcje. Kiedy wracaem z obozw brakowao mi zaprawy porannej. Uwaam, e te obozy to niezapomniana przygoda.</div>
                    <div className='section__author'><br></br>ukasz Pszonak</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">W trakcie obozu nie chc wraca do domu. Zawsze niezwykle cieszyam si, e w kocu nadeszy moje ulubione dwa tygodnie w roku. Byam troch niewyspana i zawsze mylaam, jak wymiga si od porannych zapraw. Teraz czuj dum z tego, czego dokonaam, mam w gowie mas przemyle dotyczcych nauki, problemw wiata. Ciesz si, e trafiam na ludzi, ktrzy postrzegaj wiat podobnie do mnie. Obozowa przestrze to jedyne miejsce, ktre jest a tak wypenione mioci i szacunkiem do drugiego czowieka <span role='img' aria-label='heart'></span> zachca ludzi do nauki nie poprzez przymus, a przez ch rozwoju, pobudzanie ciekawoci wiata i kreatywnoci. Filarem tego obozu s ludzie, rodzinna atmosfera, brak jakiegokolwiek skrpowania, poczucie zrozumienia, MIO </div>
                    <div className='section__author'><br></br>Agnieszka Dopieraa</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">Na obozie zawsze czuam, e jestem tak po prostu... na miejscu. Jestem tu, gdzie powinnam by, w odpowiednim czasie, z odpowiednimi ludmi. Po powrocie czuam si jak balonik napeniony szczciem. Tylko troch al, e ju koniec. Tu uczymy si, ale tak nie z przymusu (moe troszk z poczucia obowizku... <span role='img' aria-label='laughing face'></span>). Spdzamy mnstwo czasu razem, robic naprawd przyjemne rzeczy,  gramy w siatk, w planszwki, pywamy w jeziorze, a czas upywa wrd fantastycznych ludzi. Poznajemy innych, ale rwnie siebie, jacy jestemy i podoba nam si to, co odkrywamy. Filarem obozu jest kadra. Oni wiedz o co w tym chodzi. Szanuj wszystkich uczestnikw, przyjmuj kadego takim jakim jest, respektuj nasz indywidualizm, wspieraj nasze inicjatywy. Moim zdaniem misj obozu jest rozwj na kadej paszczynie, speniona potrzeba przynalenoci do grupy ciekawych, inteligentnych i czsto lekko zwariowanych ludzi.</div>
                    <div className='section__author'><br></br>Jagna Kicya</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">Szczerze polecam obozy naukowe organizowane przez p. Natali Zarask i p. Ignacego Rejmaka. S zawsze bardzo dobrze przygotowane. Pani Natalia i Pan Ignacy maj wietny kontakt z modzie. Opieka naukowa jest na bardzo wysokim poziomie. Modzie ma moliwo pracowa ze specjalistami i pasjonatami w swej dziedzinie. Crka uczestniczya w czterech obozach. Kady z nich to wietnie wykorzystany czas na nauk oraz na mdr zabaw. Decydujc si na obz na pewno nie bdziecie zawiedzeni.</div>
                    <div className='section__author'><br></br>Agnieszka Bartosiak</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">Podczas obozw panuje niepowtarzalny klimat w grupie, ktra liczy kilkadziesit osb, w rnym wieku i o rnych charakterach. Mam ogromn przyjemno od lat osobicie przyglda si tym niezwykym relacjom, przebywajc na wypoczynku w czasie trwania letniego obozu naukowego. Byli absolwenci wracaj do tego miejsca, aby spotka si ze swoimi nauczycielami i kolegami, bo jak mwi: tu jest jaka magia. Jako rodzic jestem ogromnie wdziczny twrcom za projekt Wyobrania waniejsza od wiedzy, ktry pozwoli uksztatowa moje dzieci w taki sposb, e dzisiaj jestem z nich bardzo dumny.</div>
                    <div className='section__author'><br></br>Marek Targoski</div>
                    <h1 className='quote'>~</h1>
                </div>
                <div className="recomendations__section">
                    <div className="section__text">W ostatnich latach mj syn uczestniczy w obozie naukowym w Wielu, organizowanym przez ciekawych ludzi oddanych modziey i starajcych si o  rozwj ich wiedzy. Wspaniaa atmosfera i ciekawe zajcia dydaktyczne z zakresu fizyki, informatyki czy matematyki, wzbogacone byy o aktywno fizyczn : codzienna przebieka, kpiel w jeziorze czy gra w siatkwk . Atrakcj corocznego obozu byy organizowane spywy kajakowe, integrujce nie tylko modzie, ale i ich rodzicw . Syn zawsze wraca z obozu peen zapau i entuzjazmu. Myl, e  wynikao to z tego w jaki sposb traktowano go we Wielu. Zawsze mia moliwo wyraania swoich opinii bez obaw o krytyk. Konstruktywne rozmowy i dyskusje midzy modzie i wykadowcami wydobyway z nich to co najlepsze. Obz daje moliwo rozwoju i poszerzenia nie tylko wiedzy, ale grona przyjaci i poznania interesujcych ludzi.</div>
                    <div className='section__author'><br></br>Katarzyna i Aleksander Becker</div>
                    <h1 className='quote'>~</h1>
                </div>
            </div>
        </div>
    );
}

export default RecomendationsContent;