import React from 'react';

import { Swiper, SwiperSlide } from 'swiper/react/swiper-react'; // problem with route - added  '/swiper-react' at the end
import SwiperCore, { Navigation, Pagination } from 'swiper';

import WbIcon from '../assets/img/forMinors-wb.svg';
import TikTokIcon from '../assets/img/forMinors-tik-tok.svg';
import YtIcon from '../assets/img/forMinors-yt.svg';
import FbIcon from '../assets/img/forMinors-fb.svg';
import ForMinorsLogo from '../assets/img/forMinorsLogo.png';
import ForMinorsSlider1 from '../assets/img/forMinorsSlider1.jpg';
import ForMinorsSlider2 from '../assets/img/forMinorsSlider2.jpg';
import ForMinorsSlider3 from '../assets/img/forMinorsSlider3.jpg';
import Triangle from '../assets/img/triangle.svg';

SwiperCore.use([Navigation, Pagination]);

const ForMinorsContent = () => {
  return (
    <>
      <div className="about about-forMinors">
        <h2 className="about--header header">O PROJEKCIE</h2>
        <p className="about--text about--text--forMinors">
          <bold>Fizyka dla nieletnich  dowiadczenia</bold>{' '}
          (www.underage.pro) unikalny projekt pod patronatem honorowym Ministra
          Edukacji i Nauki przeznaczony dla nauczycieli fizyki, przedmiotw
          zawodowych oraz dla uczniw fizyki z klas 7-8 szk podstawowych,
          modziey z klas 1-4 licew oraz 1-5 technikw, a take ze szk
          zawodowych.
        </p>
        <p className="about--text about--text--forMinors">
          Projekt obejmuje 100 dowiadcze z caego zakresu nauczania fizyki,
          ktre w kolejnych miesicach bd uzupeniane o kolejne paczki
          eksperymentow, dostpnych bezpatnie (a take do cignicia) na
          stronie{' '}
          <a href="//underage.pro/fizyka/doswiadczenia">
            www.underage.pro/fizyka/doswiadczenia
          </a>
          . Dowiadczenia celowo pozbawione s komentarza, by pozostawi
          nauczycielom pole do zademontrowania i opracowania ich na lekcjach.
        </p>
        <iframe
          title="movie"
          className="frame frame-forMinors"
          src="https://www.youtube.com/embed/clwS2qAU1q4"
          frameBorder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
        <p className="about--text about--text--forMinors">
          Rozszerzeniem projektu jest cykl atrakcyjnych prezentacji i omwie z
          fizyki, przeznaczonych dla uczniw, ktre dostpne s pod adresem{' '}
          {` `}
          <a href="/underage.pro/fizyka/odcinki">
            www.underage.pro/fizyka/odcinki
          </a>{' '}
          w formie kilku-kilkunastominuto-wych filmw w przystpny sposb
          wyjanionych przez dwch nauczycieli z wejherowskiego Elektryka:
          Ignacego Rejmaka i Przemysawa Rojewskiego.
        </p>
        <p className="about--text about--text--forMinors">
          Fizyka dla nieletnich powstaa po konsultacjach z nauczycielami
          fizyki, pedagogami oraz naukowcami, ktrzy wskazywali na olbrzymie
          zapotrzebowanie na takie pomoce dydaktyczne. Projekt zosta
          przygotowany w oparciu o podstaw programow.
        </p>
        <p className="about--text about--text--forMinors">
          100 dowiadcze z zakresu kinematyki, dynamiki, bryy sztywnej,
          statyki, drga i fal, elektrostatyki oraz prdu staego. Ju wkrtce
          kolejne eksperymentyz tych i kolejnych dziaw: prdu przemiennego,
          magnetyzmu, hydrostatyki, termodynamiki, optyki oraz fizyki jdrowej.
          Cotygodniowe odcinki na YouTube  premiera w kad rod 
          przeznaczone dla uczniw, ktre z dystansem i humorem tumacz nawet
          najtrudniejsze zagadnienia.
        </p>
        <iframe
          title="movie"
          className="frame frame-forMinors"
          src="https://www.youtube.com/embed/U1_CFAE_tjY"
          frameBorder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
        <div className="forMinors-social">
          <h3>odwied nasz stron i media spoecznociowe:</h3>
          <div className="social-continer">
            <a href="//underage.pro">
              <img src={WbIcon} alt="Sprawd nasz stron!" />
            </a>
            <a href="//tiktok.com/@underage.pro">
              <img src={TikTokIcon} alt="Sprawd naszego Tik Toka!" />
            </a>
            <a href="//youtube.com/channel/UChkmmDRRdNvkrLB6lFctbgw">
              <img src={YtIcon} alt="Sprawd nasz kana na Youtube!" />
            </a>
            <a href="//facebook.com/NieletniInzynierowie">
              <img src={FbIcon} alt="Sprawd naszego Facebooka!" />
            </a>
          </div>
        </div>
      </div>
      <div className="forMinors-tutors">
        <h2 className="about--header header">PROWADZCY </h2>
        <p className="about--text">
          Prowadzcymi i realizujcymi dowiadczenia s Ignacy Rejmak i
          Przemysaw Rojewski, charyzmatyczni nauczyciele z wieloletnim staem i
          sukcesami. Potrafi wcign uczniw w nieatw dziedzin, jak jest
          fizyka. Tworz doskonay, peen humoru i dystansu do siebie duet.
          <ul>
            <li>
              Ignacego obserwowalimy podczas 4 lat pracy nad filmem
              dokumentalnym Nieletni inynierowie.
            </li>
            <li>
              Przemek jest magikiem dowiadcze, potrafi zilustrowa kade
              zjawisko dziki czemu fizyka przestaje by wiedz tajemn. Jej
              prawa staj si zrozumiae.
            </li>
          </ul>
          Strony merytorycznej na planie i poza nim doglda dr in. Grzegorz
          ukasik. Dowiadczenia wykonywane s w jednej z najlepszych pracowni w
          kraju, w wejherowskim Elektryku.
        </p>
        <div className="forMinors-slider">
          <Swiper
            slidesPerView={1}
            navigation={{
              nextEl: '.nextNav',
              prevEl: '.prevNav',
            }}
            loop={true}
            pagination={{
              el: '.pagination',
              clickable: true,
            }}
            grabCursor
          >
            <div className="swiper-nav">
              <div className="pagination"></div>
            </div>
            <div className="prevNav">
              <img src={Triangle} alt="Strzaki suce do przewijania" />
            </div>

            <div className="nextNav">
              <img src={Triangle} alt="Strzaki suce do przewijania" />
            </div>
            <SwiperSlide>
              <div className="slider-slide">
                <img
                  src={ForMinorsSlider1}
                  alt="Poznaj naszych prowadzcych!"
                />
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="slider-slide">
                <img
                  src={ForMinorsSlider2}
                  alt="Poznaj naszych prowadzcych!"
                />
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="slider-slide">
                <img
                  src={ForMinorsSlider3}
                  alt="Poznaj naszych prowadzcych!"
                />
              </div>
            </SwiperSlide>
          </Swiper>
        </div>
      </div>

      <div className="forMinors-logo">
        <a href="//underage.pro">
          <img
            src={ForMinorsLogo}
            alt="Logo projektu 'Fizyka dla nieletnich'"
          />
        </a>
      </div>
    </>
  );
};

export default ForMinorsContent;
