import React from 'react'

const OfferExpandedOne = () => {
    return (
        <div className="offer__section1">
            <div className="offer__expanded offer__expanded1">
                <div className="expanded__title title__border">1. Misja</div>
                <p className="expanded__text">
                    Wychodzc naprzeciw oczekiwaniom modziey i potrzebom rynku pracy stworzylimy Osad Wiedzy. Diagnoza potrzeb rynku pracy jasno wskazuje, e umiejtnoci i wiedza z zakresu przedmiotw cisych jest dzisiaj niezbdna z uwagi na rozwj nowej technologii. Z drugiej strony modziey brakuje kompetencji spoecznych, ktre s zaniedbane przez wspczesny system edukacji. Dlatego te, opierajc si na 20-letnim dowiadczeniu w pracy pedagogicznej, konsultacjach z nauczycielami, trenerami, realizowanych wczeniej podobnych projektach stworzono przestrze, w ktrej ksztatujemy wszystkie niezbdne dzisiaj kompetencje.<br></br><br></br>
                    Osada wiedzy to przestrze dla uczniw uzdolnionych z zakresu fizyki i matematyki, uczniw, ktrzy wi swoj przyszo wanie z tymi naukami. Ofert poszerzylimy o zajcia z programowania a take warsztaty rozwijajce warto wasn i pewno siebie oparte na narzdziach coachingowych. Rozwj  interpersonalny, emocjonalny i kompetencyjny s dla nas gwnym celem. Nasz misj jest zdobywanie wiedzy opartej na relacji i mentoringu.<br></br><br></br>
                    Osada Wiedzy opiera si na pracy wykwalifikowanych nauczycieli, z bogatym dorobkiem zawodowym, majcych uprawnienia w zakresie trenerskim, pracujcych i koordynujcych obozy naukowe od 2010r.  Dziki pracownikom Uniwersytetu Gdaskiego i Uniwersytetu Morskiego uczniowie bd mogli korzysta bezporednio z wiedzy naukowcw.
                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>
            <div className="offer__expanded offer__expanded2">
                <div className="expanded__title">2. Uczestnicy</div>
                <p className="expanded__text">
                    Uczestnikami obozu mog by uczniowie po VI klasie Szkoy Podstawowej. Wymagana jest opinia nauczyciela matematyki (w przypadku uczniw po VI klasie SP) oraz fizyki (po VII kl. SP). Uczestnicy w dniu przyjazdu zostan podzieleni na grupy zgodnie z ich umiejtnociami i kompetencjami. Nie obowizuje przy podziale wiek uczestnika. Grupy bd liczyy maksymalnie 15 osb.
                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>
        </div>
    );
}

export default OfferExpandedOne;