import React from 'react';

import IRimg1 from '../assets/img/ir1.jpg';
import IRimg2 from '../assets/img/ir2.jpg';
import IRimg3 from '../assets/img/ir3.jpg';

import NZimg1 from '../assets/img/nz1.jpg';
import NZimg2 from '../assets/img/nz2.jpg';
import NZimg3 from '../assets/img/nz3.jpg';

import BPimg1 from '../assets/img/bp1.jpg';
import BPimg2 from '../assets/img/bp2.jpg';
import BPimg3 from '../assets/img/bp3.jpg';

import ASimg1 from '../assets/img/as1.jpg';
import ASimg2 from '../assets/img/as2.jpg';
import ASimg3 from '../assets/img/as3.jpg';

import WMimg1 from '../assets/img/wm1.jpg';
import WMimg2 from '../assets/img/wm2.jpg';
import WMimg3 from '../assets/img/wm3.jpg';

import PRimg1 from '../assets/img/pr1.jpg';
import PRimg2 from '../assets/img/pr2.jpg';
import PRimg3 from '../assets/img/pr3.jpg';

import CZ1img from '../assets/img/cz1.jpg';
import CZ2img from '../assets/img/cz2.jpg';
import CZ3img from '../assets/img/cz3.jpg';

const TeamPerson = () => {
	const teamPersons = [
		{
			name: 'Ignacy Rejmak',
			img1: IRimg1,
			img2: IRimg2,
			img3: IRimg3,
			text: [
				'- nauczyciel fizyki w technikum w Wejherowie i w liceum w Lborku. Przez 10 lat pracowa w gimnazjum uczc matematyki i fizyki. Przez ten czas pracowa z uczniami szczeglnie uzdolnionymi w kierunku przedmiotw cisych, przygotowujc ich do konkursw i olimpiad zarwno fizycznych, matematycznych i informatycznych. Przez ten czas wychowa ponad 20 laureatw i finalistw, gwnie konkursw fizycznych. Od 10 lat wsporganizuje obozy naukowe. Wsppracuje z Uniwersytetem Gdaskim, wsporganizujc konferencje metodyczne dla nauczycieli fizyki z wojewdztwa pomorskiego.',
				<br />,
				<br />,
				'Ignacy Rejmak jest bohaterem niezwykego filmu dokumentalnego, ktry swoj premier mia we wrzeniu 2020. Nieletni inynierowie to historia przemiany, odkrywania pasji i zdobywania wiedzy. To opowie o mioci do nauki i mocy relacji, tak bardzo nam dzisiaj potrzebnej. Film pojawi si na midzynarodowych festiwalach otrzymujc wiele cennych nagrd takich jak: Festiwal Cineminha B. w Brazylii, International CHINH INDIA KIDS FILM FESTIVAL w Indiach, Delco Film Festival w USA, Polish Film Festival Los Angeles, Festiwalu Filmowym Zlin w Czechach, Satisfied Eye International Film Festival w Wielkiej Brytanii gdzie sta si Najlepszym Filmem Dokumentalnym, a Ignacy Rejmak otrzyma na tym festiwalu Nagrod Jury Satisfied Eye International Film Festival.',
				<br />,
				'Wicej o filmie:',
				<br />,
				<a href='/underage.pro/inzynierowie/'>
					https://underage.pro/inzynierowie/
				</a>,
				<br />,
				<br />,
				'Ponadto Ignacy Rejmak wraz z Przemysawem Rojewskim uczestniczy w unikalnym projekcie edukacyjnomedialnym dla uczniw i nauczycieli klas szk podstawowych oraz rednich realizowanym w Powiatowym Zespole Szk nr 2 im. Bohaterskiej Zaogi ORP Orze w Wejherowie Fizyka dla nieletnich Projekt zakada tworzenie 12-minutowych odcinkw dla uczniw, ktre bd dostpne na Facebooku oraz kanale YouTube. Ich celem jest wyjanianie zjawisk fizycznych w prosty, przystpny, a zarazem ciekawy sposb, a w efekcie zachcenie modych ludzi do zawodw i kierunkw technicznych. Wicej o projekcie ',
				<br />,
				<a href='//underage.pro/fizyka '>https://underage.pro/fizyka </a>,
				<br />,
				<br />,
				'Przez swoje niekonwencjonalne metody nauczania, niezwyk osobowo a take relacje z modzie, czsto porwnywany do bohatera ksiki Stowarzyszenie Umarych Poetw. Twierdzi, e ma wicej szczcia ni rozumu, gdy w szkoach w ktrych pracuje zawsze trafia na nauczycieli, od ktrych cigle moe si bardzo wiele nauczy.',
				<br />,
				'W Osadzie Wiedzy prowadzi zajcia z fizyki.',
			],
		},
		{
			name: 'Natalia Zaraska',
			img1: NZimg1,
			img2: NZimg2,
			img3: NZimg3,
			text: [
				'- coach, trener i mwca John Maxwell Team, trener psychologii pozytywnej, facylitator job craftingu, doradca zawodowy. Zakochana w zmianie. Jej mentorami s Paul Martinelli, Joe Dispenza oraz Gregg Braden . To od nich czerpie wiedz, to oni nadaj kierunek jej pracy. Prowadzi terapi zgodn z nurtem Racjonalnej Terapii Zachowania, ktra w gwnej mierze opiera si na pracy z przekonaniami. Z pierwszego wyksztacenia jest pedagogiem i przez 22 lata prowadzia modych ludzi w okresie dojrzewania, wspieraa take rodzicw, prowadzc szkolenia i warsztaty. Wacicielka Akademii Rozwoju Prosperity w ramach, ktrej dziaa Soward Smart Kids Academy oraz Akademia Lidera Still Up. Wspwacicielka firmy, w ramach ktrej organizuje obozy naukowe Osada Wiedzy. Jedna z kreatorek Festiwalu Inspiracje. Prezes Stowarzyszenia FPUNKT. Finalistka konkursu im. Ireny Sendlerowej Za naprawianie wiata. W 2021r. zostaa zakwalifikowana przez Centrum Edukacji Obywatelskiej oraz Ambasad Amerykask do programu International Visitor Leadershi i jako jedna z szeciu nauczycieli i nauczycielek, wybranych spord wszystkich finalistw i laureatw tego konkursu, odbdzie 21-dniow studyjn wizyt w USA. Za swoje dziaania otrzymaa Medal Komisji Edukacji Narodowej.  Jej pasj s musicale, ktre tworzy wsplnie z modzie. Jej spektakle stany na Stadionie Narodowym (spektakl Szukajc siebie) a take na duej scenie Teatru Muzycznego w Gdyni (musical Szepty serc). W Osadzie Wiedzy prowadzi warsztaty pewnoci siebie i wartoci wasnej, ktre le w jej obszarze zainteresowa naukowych.',
				<br />,
				<br />,
				<a href='//nataliazaranska.pl'>www.nataliazaranska.pl</a>,
			],
		},
		{
			name: 'Bogusaw Pranszke',
			img1: BPimg1,
			img2: BPimg2,
			img3: BPimg3,
			text: [
				'- dr hab., prof. nadz. Uniwersytetu Morskiego w Gdyni. Pracuje na Wydziale Mechanicznym UMG, w Katedrze Fizyki. Fizyk, specjalno - fizyka dowiadczalna. Pracowa z uczniami szczeglnie uzdolnionymi w kierunku przedmiotw cisych, przygotowujc ich do olimpiad fizycznych. Zainteresowania: nauczanie oraz  popularyzacja fizyki i astronomii, filmy i ksiki sf, gry planszowe oraz, od ponad 15 lat, AoC (gra komputerowa Age of Empires II: The Conquerors).',
			],
		},
		{
			name: 'Agata Skrzypalik',
			img1: ASimg1,
			img2: ASimg2,
			img3: ASimg3,
			text: [
				'- nauczycielka matematyki w lborskiej szkole. Absolwentka projektu Wyobrania waniejsza od wiedzy, w ramach ktrego realizowane s obozy naukowe. Jest przekonana, e kadego czowieka mona zainteresowa matematyk. Zaraa swoim entuzjazmem do tej dziedziny nauki. Uwielbia organizowa wszelkiego rodzaju eventy, angauje si we wszystko, co tylko moe. Posiada niezliczone pokady radoci i umiechu, ktre wykorzystuje na kadym kroku swojego ycia. Kad woln chwil spdzaaby na grskich szlakach. Cigle w podry, zafascynowana piknem wiata i czowieka. Agata Skrzypalik jest take trenerk metody Brainy Abacus w Soward Smart Kids Academy w Lborku. Brainy Abacus to prowadzony w kilku tysicach szk na caym wiecie, oparty na naukowych podstawach midzynarodowy program treningu mzgu, matematyki mentalnej, pamici i bardzo szybkiego liczenia. Metoda Brainy Abacus skutecznie poprawia koncentracj, skupienie i pami, poprawia umiejtno logicznego mylenia. W osadzie wiedzy spenia swoje marzenie prowadzc zajcia z matematyki.',
			],
		},
		{
			name: 'Wojciech Malicki',
			img1: WMimg1,
			img2: WMimg2,
			img3: WMimg3,
			text: [
				'- swj pierwszy program komputerowy napisa w wieku 12 lat na komputerze Commodore 64. Od tego czasu z ma przerw uczy si nowych jzykw programowania i nieustannie poszerza swoj wiedz i umiejtnoci, przechodzc od Basica przez Pascala do C++ i Pythona po drodze zahaczajc o JavaScript i VBA. Od 2010 roku pracuje jako nauczyciel, nie tylko w szkole, ale take w projektach zwizanych z prowadzeniem zaj dla modziey uzdolnionej w Supsku, Lborku i Kartuzach. Od 2012 roku uczestniczy w obozach naukowych w czasie wakacji. Przygotowywa rwnie uczniw do olimpiad informatycznych, w tym do olimpiady z programowania zespoowego. Aby podnie swoje umiejtnoci uczestniczy w warsztatach dla nauczycieli olimpiady informatycznych. Ukoczy studia podyplomowe z zakresu testowania oprogramowania, programowania aplikacji internetowych oraz cyberbezpieczestwa. Najbardziej ceni u siebie zapa do uczenia si nowych rzeczy. W Osadzie Wiedzy prowadzi zajcia z programowania.',
			],
		},
		{
			name: 'Przemysaw Rojewski',
			img1: PRimg1,
			img2: PRimg2,
			img3: PRimg3,
			text: [
				'- nauczyciel fizyki w Powiatowym Zespole Szk nr 2 w Wejherowie. W zawodzie od 20 lat Od 11 lat pracuje z uczniami szczeglnie uzdolnionymi w ramach Projektu Zdolni z Pomorza przygotowujc ich do konkursw i olimpiad  szczeglnie w czci dowiadczalnej. Przez ten czas wychowa wielu laureatw i finalistw. Jest dowiadczonym egzaminatorem OKE w Gdasku od 16 lat uczestniczy w pracach komisji egzaminacyjnej egzaminu maturalnego z fizyki. Rwnie przygotowuje z powodzeniem uczniw do tego egzaminu. Od kilku lat tworzy pracowni fizyczn z prawdziwego zdarzenia wyposaon w najnowoczeniejszy dostpny sprzt pomiarowy. W czasie wolnym nie moe oby si bez treningw biegowych jest zwycizc wielu biegw dugodystansowych i maratonw.',
			],
		},
		{
			name: 'Cay zesp',
			img1: CZ1img,
			img2: CZ2img,
			img3: CZ3img,
			text: [''],
		},
	];
	return (
		<>
			{teamPersons.map(person => {
				return (
					<div key={person.name} className='team__section'>
						<div className='team__name'>{person.name}</div>
						<div className='team__imgs'>
							<div className='team__img'>
								<img src={person.img1} alt='' />
							</div>
							<div className='team__img'>
								<img src={person.img2} alt='' />
							</div>
							<div className='team__img'>
								<img src={person.img3} alt='' />
							</div>
						</div>
						<div className='team__text'>
							<p>{person.text}</p>
						</div>
					</div>
				);
			})}
		</>
	);
};

export default TeamPerson;
