import React from 'react'

const OfferExpandedTwo = () => {
    return (
        <div className="offer__section5">
            <div className="offer__title">Program obozu</div>
            <div className="offer__expanded offer__expanded3">
                <div className="expanded__title title__border">1. Fizyka</div>
                <p className="expanded__text">
                    Podobno fizyka jest trudna i nikt nigdy jej nie rozumia. Wanie dlatego nasze zajcia z fizyki to pocztkowo opowiadanie bajek. Staramy si wprowadzi w zagadnienie jzykiem zrozumiaym dla modych ludzi, pobudzi ich wyobrani i zmusi do mylenia. Najwaniejsze jest zrozumienie omawianego zagadnienia na poziomie moliwie najbliszym intuicji. I twierdzimy, e da si to zrobi jzykiem zrozumiaym. Ale poniewa nie samymi bajkami czowiek yje, wic pniej prbujemy to wszystko uj w odpowiedni dla wieku modych ludzi aparat matematyczny. I tam dopiero zaczyna si taka zabawa, jak wszyscy najbardziej lubi. 
                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>
            <div className="offer__expanded offer__expanded4">
                <div className="expanded__title title__border">2. Matematyka</div>
                <p className="expanded__text">
                    Zajcia z matematyki to przede wszystkim rozwizywanie zada, w ktrych stawiamy na zrozumienie, a nie zapamitywanie schematw. Tym samym wiczymy logiczne mylenie i rozwizywanie problemw. Na obozie wchodzimy w zagadnienia z ktrymi uczniowie spotkaj si ju na samym starcie nowego roku szkolnego. Chcemy w ten sposb zapewni im pewno swoich umiejtnoci i spokojny powrt na lekcje matematyki. Jednoczenie dajc konkretne narzdzia do dziaania w sferze fizyki.
                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>
            <div className="offer__expanded offer__expanded5">
                <div className="expanded__title title__border">3. Programowanie</div>
                <p className="expanded__text">
                Zajcia z programowania s prowadzona w zalenoci od zaawansowania uczestnikw. Moe to by nauka podstaw programowania i algorytmw (w jednym z trzech jzykw: C++, Python, JavaScript). Dla zaawansowanych moe to by rozwizywanie zada z pogranicza olimpiady informatycznej, bazujcych na strukturach danych czy grafach lub stworzenie projektu praktycznego. Kady rodzaj zaj ma na celu rozwinicie pasji zwizanych z programowaniem oraz pomoc w zrozumieniu komputerw i informatyki, a take powizanie programowania z matematyk i fizyk.
                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>
            <div className="offer__expanded offer__expanded6">
                <div className="expanded__title title__border">4. Kompetencje spoeczne</div>
                <p className="expanded__text">
                    Zajcia maj na celu rozwj osobowoci w tym midzy innymi: ksztatowanie poczucia wasnej wartoci, poczucia pewnoci siebie, nauka stawiania yciowych celw, budowania drogi do ich realizacji, ksztatowanie umiejtnoci obiektywnego selekcjonowania opinii docierajcych do uczestnikw, aby potrafili dba o poczucie wasnej wartoci, budowanie wiary we wasne moliwoci, ksztatowanie motywacji do osigania celw, umiejtnoci budowania wasnych pozytywnych afirmacji, prawidowych relacji z innymi, postaw asertywnych celem obrony wasnej przestrzeni yciowej.
                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>
            <div className="offer__expanded offer__expanded7">
                <div className="expanded__title">5. Czas wolny</div>
                <p className="expanded__text">
                Czas wolny uczestnikw obozu to korzystanie z play, boiska do siatkwki, kpiel w jeziorze. Czas wolny przeznaczony jest take na indywidualne konsultacje z nauczycielami prowadzcymi zajcia, mentoring a take dyskusje naukowe uczestnikw obozu. W trakcie obozu rozegrany zostanie take <b>Turniej Osady</b>. Uczestnicy zostan podzieleni na grupy. W trakcie obozu bd rozwizywa rnorodne zadania z matematyki, fizyki i programowania. W dniach, w ktrych nie bd odbywa si wykady, uczestnicy bd przystpowali do zada grupowych np. logiczne i strategiczne gry planszowe.
                </p>
                <div className="expanded__cross">
                        <span className='cross__cross1'></span>
                        <span className='cross__cross2'></span>
                </div>
            </div>
        </div>
    );
}

export default OfferExpandedTwo;
